<template>
	<view class="all">
		<view class="head">
			{{data.head}}
		</view>
		<form @submit="formSubmit">
			<input name="input" class="title" maxlength="16" confirm-type="done" type="text" placeholder="请输入标题"
				auto-blur='true' />
			<textarea class="content" name="textarea" confirm-type="done" type="text" :placeholder="placeholder"
				auto-blur='true'></textarea>
			<view class="images">
				<view class="imageItem">
					<image :lazy-load="true" v-if="images.length>0" v-for="(item,index) in images" :key="index"
						:src="item" mode="" @click="remove(index)"></image>
				</view>
				<view class="selectImages" @click="chooseImages">
					<image src="../../static/icon/照相机.png" mode=""></image>
				</view>
			</view>
			<view class="day">
				<view>{{data.day}}日期</view>
				<picker name="picker" mode="date" fields="day" @change="bindDateChange">
					<view>{{date}}</view>
				</picker>
			</view>
			<button class="submit" form-type="submit">发布</button>
		</form>
	</view>
</template>

<script>
	import {
		computed
	} from "vue";
	import {getDate} from '../../untils/date.js'
	export default {
		data() {
			const currentDate = getDate({
				format: true
			})
			return {
				images: [],
				date: currentDate,
				data: {
					id: 0,
					head: "",
					content: "",
					day: ""
				},
				placeholder: '',
			};
		},
		onLoad(option) {
			if (!option.id) {
				uni.switchTab({
					url: '../Home/Home'
				})
			}
			this.data = option
			this.placeholder = `今天发生了什么？${this.data.content}详情在这里请填写吧~`
		},

		methods: {
			formSubmit(e) {
				let url = this.data.id == 1 ? '../Home/Home' : '../HeardEvent/HeardEvent'
				let value = e.detail.value
				value.image = this.images
				value.date = this.date
				if (value.input == "" || value.textarea == "") {
					console.log(value)
					uni.showModal({
						title: '提示',
						content: '请完整填写内容'
					})
				} else {
					uni.switchTab({
						url: url,
						success: () => {
							value.image.forEach((item) => {
								uni.saveFile({
									tempFilePath: item
								})
							})
							const eventChannel = this.getOpenerEventChannel();
							eventChannel.emit('someEvent', {
								data: value
							});
						}
					})
				}


			},
			chooseImages() {
				uni.chooseImage({
					success: (res) => {
						res.tempFilePaths.forEach((tempFilePath) => {
							var tempFilePaths = tempFilePath
							uni.saveFile({
								tempFilePath: tempFilePaths,
								success: (res) => {
									var savedFilePath = res.savedFilePath;
									this.images.push(savedFilePath)

								}
							});
						})
					}
				})
			},
			remove(index) {
				this.images.splice(index, 1)
			},
			bindDateChange(e) {
				this.date = e.detail.value
			}
		}
	}
</script>

<style lang="scss">
	.all {
		margin: auto 20rpx;

		.title {
			width: 750rpx;
			margin: 20rpx auto;
			padding-bottom: 20rpx;
			font-size: 30rpx;
			border-bottom: 1px solid #eee;
		}

		.content {
			width: 750rpx;
			margin: 20rpx auto;
			font-size: 30rpx;
		}

		.images {
			display: flex;
			width: 750rpx;
			padding-bottom: 20rpx;

			.selectImages {
				width: 150rpx;
				height: 150rpx;
				background-color: #a6a6a6;
				display: flex;
				justify-content: center;
				align-items: center;

				image {
					width: 100rpx;
					height: 100rpx;
				}
			}

			.imageItem {
				image {
					position: relative;
					margin-right: 20rpx;
					width: 150rpx;
					height: 150rpx;
					pointer-events: none;

					&::before {
						color: white;
						content: '-';
						font-size: 45rpx;
						position: absolute;
						display: block;
						top: -20rpx;
						left: 120rpx;
						width: 50rpx;
						height: 50rpx;
						border-radius: 50%;
						background: black;
						pointer-events: auto;
					}
				}
			}
		}

		.day {
			border-top: 1px solid #eee;
			border-bottom: 1px solid #eee;
			display: flex;
			height: 80rpx;
			align-items: center;
			justify-content: space-between;
		}

		.submit {
			width: 350rpx;
			color: white;
			border-radius: 50rpx;
			background: #6365b7;
			margin-top: 30rpx;
		}
	}
</style>